.follow {
  background-color: var(--color-secondary-3);
  height: 70vh;
  text-align: center;
  display: grid;
  place-content: center;

  .followTitle {
    position: relative;
    font-size: 4rem;
    display: inline-block;

    .top {
      font-family: var(--font-main-en-top);
      position: relative;
      z-index: 1;
    }

    .bottom {
      font-family: var(--font-main-en-bottom);
      color: var(--color-primary);
      position: absolute;
      top: 0;
      left: 0;

    }
  }

  // 이미지
  .followImg {
    position: relative;
    height: 50vh; width: 90%;
    max-width: 350px;
    margin: 0 auto;
    transition: 0.3s;
    animation: move2 linear infinite 20s;
  }
  
@keyframes move2 {
  0% {transform: translate(0,-20px);}
  25% {transform: translate(-5px,-10px);}
  50% {transform: translate(12px,-20px);}
  75% {transform: translate(20px,-10px);}
  100% {transform: translate(0,-20px);}
}

}


.follow .followImg img{
  position: absolute;
  width: 150px; height: 250px;
  overflow: hidden;
  object-fit: cover;
}
.follow .followImg img:nth-child(1){
  left: 10%; bottom: 0; 
  transform: rotate(-10deg);
}
.follow .followImg img:nth-child(2){
  z-index: 1;
  left: 18%; bottom: 8%; 
  transform: rotate(3deg);
}
.follow .followImg img:nth-child(3){
  z-index: 2;
  left: 26%; bottom: 16%; 
  transform: rotate(7deg);
}
.follow .followImg img:nth-child(4){
  z-index: 3;
  left: 34%; bottom: 24%; 
  transform: rotate(-6deg);
}
.follow .followImg img:nth-child(5){
  z-index: 4;
  left:40%; bottom: 32%; 
  transform: rotate(10deg);
}
