.dough {
  position: relative;
  background-color: var(--color-secondary-2);
  z-index: -2;
  padding-top: 10vh;
 text-align: center;

  .doughWrapper {
    position: relative;
    width: 60%;
    margin: 0 auto;

    .doughCow {
      position: absolute;
      bottom: 0;
      left: -40px;
      width: 30%;
    }

    .doughCard {
      position: absolute;
      width: 100px;
      aspect-ratio: 1 / 1;
      top: -8%;
      right: -10%;
    }
  }

  .doughTitle {
    color: var(--color-primary);
    text-align: center;
    font-family: var(--font-main-en-sub);
    font-size: clamp(30px, 10vw, 5rem);
    text-shadow: -1px 0px var(--color-black), 0px 1px var(--color-black), 1px 0px var(--color-black), 0px -1px var(--color-black);
    padding-top: 50px;

  }

  .doughFan {
    font-family: var(--font-main-en-sub);
    font-size: clamp(8px, 3vw, 16px);
    border: 1px solid var(--color-black);
    border-radius: 50%;
    padding: 10px 20px;
    display: inline-block;
    position: absolute;
    right: 20%;
    transition: 0.3s;
    animation: move 8s linear infinite;
  }

  @keyframes move {
    100% {
      transform: rotate(360deg);
    }
  }

  .doughBg{
    position: absolute;
    bottom: 0; left: 0;
    width: 100%; height: 60%;
    border-radius: 30vw 30vw 0 0;
    background-color: var(--color-secondary);
    z-index: -1;
  
   .bottom {
    width: 100px;
    aspect-ratio: 1 / 1;
  }
}
.doughInfo {
  font-family: var(--font-main-ko);
  text-align: center;
  margin: 30px auto;
  width: 70%;
  font-weight: 600;
  font-size: 24px;
  line-height: 2.4rem;
  padding-bottom: 20px;
}


.btn {
  cursor: pointer;
  display: inline-block;
  text-align: center;
  padding-bottom: 80px;

  margin: 0 auto;
  background-color: var(--color-primary);
  padding: 8px 12px;
  font-family: var(--font-main-en-sub);
  font-size: 20px;
  border-radius: 8px;
  border: 1.5px solid var(--color-black);
  box-shadow: rgba(0, 0, 0, 1) 3px 3px;
  margin-bottom: 50px;
}

}