.repeat {
  background-color: var(--color-primary);
  position: relative;
  height: 150vh;
  padding-top: 50px;
  overflow: hidden;

  .new {
    text-align: center;
    position: relative;
    font-size: 50px;

    .newTop {
      color: var(--color-black);
      font-family: var(--font-main-en-top);
      position: absolute;
      left: 50%;
      top: 0;
      transform: translateX(-50%);
    }

    .newBottom {
      color: var(--color-secondary);
      font-family: var(--font-main-en-bottom);
    }
  }

  .cloud {
    position: absolute;
  }

  .cloud1 {
    width: 25%;
    left: 10%;
    top: 10px;
  }

  .cloud2 {
    width: 18%;
    right: 10%;
    top: 50px;
  }

  .iceBg {
    width: 40%;
    position: absolute;
    top: 10%;
    left: 50%;
    transform: rotate(16deg) translateX(-50%);
    z-index: 9;
  }

  .iceCream {
    text-align: center;
    font-size: clamp(100px, 22vw, 170px);
    line-height: clamp(100px, 22vw, 170px);

    .iceWrapper {
      position: relative;

      .top {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        font-family: var(--font-main-en-top);
      }

      .bottom {
        font-family: var(--font-main-en-bottom);
        color: var(--color-secondary-3);
      }

    }
  }

}