.join {
  text-align: center;
  background-color: var(--color-primary);
  height: 52vh;
  padding: 16px;
  overflow: hidden;

  .joinTitle {
    font-family: var(--font-main-en-sub);
    font-size: 3rem;
    margin-bottom: 50px;
  }

  .swiper {
  
    width: 100%;
    height: 100%;
  }

  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    display: flex;
    justify-content: center;
    align-items: center;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}



@media screen and (max-width: 400px) {
  .join {
    height: 30vh;
  }
}

@media screen and (max-width: 600px) {
  .join {
    height: 40vh;
  }
}