.peace {
  background-color: var(--color-primary);
  font-family: var(--font-main-ko);
  text-align: center;
  margin: 0 auto;
  height: 150vh;
  position: relative;

  .imgPeace {
    width: 40%;
    aspect-ratio: 1 / 1.5;
    object-fit: cover;
    transform: rotate(-15deg);
    margin: 200px 0 100px;
  }

  .peaceTitle {
    font-size: 30px;
    font-weight: 800;
    line-height: 1.4;
    white-space: pre-wrap;
  }

  p {
    width: 70%;
    margin: 0 auto;
    line-height: 1.6rem;
    margin: 30px auto;
  }

  .imgPeace02 {
    margin-top: 50px;
    width: 70%;
    max-width: 400px;
    transform: rotate(10deg);
    
  }

  .btn {
    cursor: pointer;
    display: inline-block;
    text-align: center;
    padding-bottom: 0px;

    margin: 0 auto;
    background-color: var(--color-primary);
    padding: 8px 12px;
    font-family: var(--font-main-en-sub);
    font-size: 20px;
    border-radius: 8px;
    border: 1.5px solid var(--color-black);
    box-shadow: rgba(0, 0, 0, 1) 3px 3px;
    margin-bottom: 50px;
  }

  .heart {
    position: absolute;
    top: 10%;
    right: 20%;
    width: 10%;
  }

  .icecream01 {
    position: absolute;
    top: 40%;
    left: 18%;
    width: 8%;
  }
}

@media screen and (max-width: 650px) {
  .peace {
    height: 140vh;
    .icecream01 {
      top: 30%;
    }
  }
}

@media screen and (max-width: 550px) {
  .peace {
    height: 130vh;
  }
}
@media screen and (max-width: 420px) {
  .peace {
    height: 120vh;
  }
}