// Footer.scss

footer {
  background-color: var(--color-primary);
  .delivery {
    background-color: var(--color-black);
    color: var(--color-primary);
    text-align: center;
    font-family: var(--font-main-en-sub);
    font-size: 2rem;
    padding-top: 50px;

    .deliverysearch {
      border: 1.5px solid var(--color-primary);
      width: 80%;
      border-radius: 8px;
      display: flex;
      justify-content: space-between;
      margin: 20px auto;
      align-items: center;
      font-size: 24px;
      padding: 5px 10px;

      .font {
        color: var(--color-secondary-3);
        opacity: 0.4;
      }

      .deliverybtn {
        width: 24px;
        height: 24px;
        background-color: var(--color-primary);
        border-radius: 8px;
        padding: 5px;
        transition: 0.3s;
      }
      .deliverybtn:hover {
        transform: translateX(3px);
      }
    }
  }

  .wave {
    background-color: var(--color-black);
    overflow: hidden;
    position: relative;
    height: 10vh;
    img {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 250%;
      animation: wave 60s linear infinite;
    }
  }

  @keyframes wave {
    100% {
      transform: translateX(300px);
    }
  }

  .footerlogo {
    margin: 0 auto;
    text-align: center;
    img {
      width: 80%;
      padding: 50px 0;
    }
  }
  .info {
    font-family: var(--font-main-en-sub);
    font-size: 1.4rem;
    color: var(--color-black);
    opacity: 0.9;
    display: flex;
    justify-content: space-between;
  
    padding-bottom: 20px;
    a {
      display: block;
      padding: 10px 16px;
    }
  }
}