.typo {
  height: 100vh;
  color: var(--color-primary);
  text-align: center;
  font-family: var(--font-main-en-sub);
  font-size: clamp(30px, 15vw , 8rem);
  text-shadow: -1px 0px var(--color-black), 0px 1px var(--color-black), 1px 0px var(--color-black), 0px -1px var(--color-black);

  display: flex;
  flex-flow: column nowrap;
  overflow: hidden;
  justify-content: flex-end;
  padding-bottom: 10vh;
  position: relative;

  .typoBg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .typoTop,
  .typoBottom {
    position: relative;
    z-index: 1;
    white-space: nowrap;
    transform: translateX(0);
    transition: transform 0.2s ease;
  }
}
