header {
  width: 100%;
  height: 60px;
  max-width: 768px;
  display: flex;
  justify-content: space-between;
  padding: 0 16px;
  position: fixed;
  top: 0;
  z-index: 9999;
  transform: translateY(0);
  transition: 0.3s;

  img {
    width: 120px;
    background-size: contain;
    height: 60px;
  }

  nav {
    font-family: var(--font-main-en-sub);
    height: 30px;
    align-self: center;

    .gnb {
      font-size: 30px;
      position: absolute;
      left: 0;
      top: 60px; /* 헤더 바로 아래 */
      width: 100%;
      background-color: var(--color-primary); /* 메뉴 배경 색상 */
      display: flex;
      flex-direction: column;
      list-style: none;
      padding: 20px;
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.3s ease, visibility 0.3s ease;
    }

    .menu {
      position: absolute;
      top: 15px;
      right: 16px;
      z-index: 10;
      cursor: pointer;
      text-align: right;  background-color: var(--color-secondary-3);
      text-align: center;
      padding: 8px 12px;
      text-transform: uppercase;
      border: 1.5px solid var(--color-black);
      color: var(--color-black);
      border-radius: 8px;
    }

  
  }

  &.toggle-btn {
    height: 100vh;
    background-color: var(--color-primary);
    align-content: flex-start;

    nav {
      padding-top: 12px;
      align-items: flex-start;
      .gnb {
        opacity: 1;
        visibility: visible;
        display: block;
        li {
          margin: 40px 0;
        }
      }
    }
  }

  &.scrolling-down {
    transform: translateY(-60px);
    transition: transform 0.3s;
  }
}