.fint {
  background-color: var(--color-secondary-3);
  position: relative;
  height: 70vh;
  min-height: 400px;
  overflow: hidden;

  .fintCenter {
    margin: 0 auto;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    .fintInfo {
      font-family: var(--font-main-ko);
      font-size: clamp(16px, 4vw, 24px);
      font-weight: 600;
    }

    .fintTitle {
      position: relative;
      font-size: 4rem;
      display: inline-block;

      .top {
        font-family: var(--font-main-en-top);
        position: absolute;
        top: 0;
        left: 0;
      }

      .bottom {
        font-family: var(--font-main-en-bottom);
        color: var(--color-primary);
      }

    }

    .btn {
      cursor: pointer;
      display: block;
      text-align: center;
      padding-bottom: 0px;

      margin: 0 auto;
      background-color: var(--color-primary);
      padding: 8px 12px;
      font-family: var(--font-main-en-sub);
      font-size: 20px;
      border-radius: 8px;
      border: 1.5px solid var(--color-black);
      box-shadow: rgba(0, 0, 0, 1) 3px 3px;
      margin-bottom: 50px;
    }
  }

  // 이미지
  .fintWrapper {
    position: absolute;
    top: 50%; left: 50%;
    transform: translate(-50%, -50%);
    width: 70%; aspect-ratio: 1 / 1;
    transition: 0.3s;
    animation: rotate 20s infinite linear;
    .fintImg {
      position: absolute;
      width: clamp(30px, 15vw ,100px); aspect-ratio: 1 / 1;
    }
  }
  
  @keyframes rotate {
    100% {transform: translate(-50%, -50%) rotate(360deg);}
  }

  .fintWrapper .fintImg:nth-child(1) {
    top: 0; left: 50%; 
    transform:  translateX(-50%) rotate(-30deg); }
  
  .fintWrapper .fintImg:nth-child(2) {
    right: 0; top: 40%;
    transform: rotate(-20deg);
  }
  
  .fintWrapper .fintImg:nth-child(3) {
    bottom:0; right: 20%;
    transform: rotate(40deg);
  
  }
  .fintWrapper .fintImg:nth-child(4) {
    left: 20%;  bottom:0;
    transform: rotate(-30deg);
  }
  .fintWrapper .fintImg:nth-child(5) {
    left: 0; top: 40%;
    transform: rotate(30deg);
  }
  

}