/* main-intro */
.intro {
  height: 100vh; width: 100%;
  position: relative;
  display: grid;
  place-content: center;
  padding-top: 60px;
  white-space: pre-wrap;
  overflow: hidden;
  .introWrapper {
    text-align: center;
    .introInfo {
      font-family: var(--font-main-en-sub);
      color: var(--color-white);
      text-shadow: -2px 0px var(--color-black), 0px 2px var(--color-black), 2px 0px var(--color-black), 0px -2px var(--color-black);
      font-size: 20px;
      margin-bottom: 50px;
    }
    .introTitle {
      font-size: clamp(90px, 10vw, 95px);
      position: relative;
      line-height: 90px;
      .introTop{
        font-family: var(--font-main-en-top);
        color: var(--color-black);
        position: absolute;
        top: 0; left: 50%;
        transform: translateX(-50%);
      }
      .introBottom{
        font-family: var(--font-main-en-bottom);
        color: var(--color-white);
      
      }
    }
  }

  .introBtn { 
    text-align: center;
    border: 1.5px #000 solid;
    background-color: var(--color-primary);
    padding: 30px 50px;
    border-radius: 50%;
    display: inline-block;
    font-family: var(--font-main-en-sub);
    font-size: 24px;
    margin-top: 70px;
    cursor: pointer;
 
  }
  .introBg {
    position: absolute;
    z-index: -1;
    top: 0; left: 0;
    height: 100%;width: 100%;

  
  }
  .introBg iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    border: none;
    object-fit: cover; 
  }
}
