@import url(./assets/styles/reset.min.css);

@import url(./assets/styles/pretendardvariable.css);
@import url(./assets/styles/impact.css);
@import url(./assets/styles/komigo.css);




/* 변수선언 */

:root {
  /* 폰트 */
  --font-main-ko: "Pretendard Variable", 'Courier New', Courier, monospace, -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", serif;
  --font-main-en-top:  "Komigo 3D", -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", Arial, serif;
  --font-main-en-bottom:  "Komigo", -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", Arial, serif;
  --font-main-en-sub:  "impact", -apple-system, BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI", "Apple SD Gothic Neo", Arial, serif;


  /* 색상 */
  --color-primary : #ffad14;
  --color-secondary : #a3d45d;
  --color-secondary-2 : #28b8e5;
  --color-secondary-3 : #f1efe1;

  --color-black : #000;
  --color-white : #fff;

}

/* 공통 클래스 */

body {
  max-width: 768px;
  margin: 0 auto;
}


